import { onBeforeUnmount } from 'vue'
import { useBus } from '@/plugins/event/useBus'

export const useEvent = (event, callback, isCall = false) => {
  const emitter = useBus()

  const on = () => {
    if (callback) {
      emitter.on(event, callback)
    }
  }
  const off = () => {
    if (callback) {
      emitter.off(event)
    }
  }
  const emit = payload => {
    return emitter.emit(event, payload)
  }

  if (isCall) {
    on()
  }
  onBeforeUnmount(() => {
    if (isCall) {
      off()
    }
  })

  return {
    emitter,
    on,
    off,
    emit
  }
}
